.sk-the-symbol .symbol-wrapper {
  text-align: center;
}
.sk-the-symbol .symbol-section-svg {
  flex: 0 0 auto;
  margin-bottom: 0px;
  max-height: 450px;
  min-height: 530px;
}
@media screen and (min-width: 1025px) {
  .sk-the-symbol .symbol-section-svg {
    padding-right: 35px;
  }
}
@media screen and (max-width: 1024px) {
  .sk-the-symbol .symbol-section-svg {
    padding-bottom: 64px;
  }
}
.sk-the-symbol .st0 {
  fill: none;
}
.sk-the-symbol .st1 {
  fill: #002fa7;
}
.sk-the-symbol .st2 {
  fill: #17fcc5;
}
.sk-the-symbol .st3 {
  fill: #008fc5;
}
.sk-the-symbol .st4 {
  fill: #fe0249;
}